/*** ELEMENT ***/

* {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

body {
  background-color: #ffe9d6;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #654062;
  font-family: 'Noto Sans JP', sans-serif;
}

a {
  color: #86aba1;
}

/*** CLASS ***/

.wrapper-grid {
  margin: 0;
  border: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 4fr;
}

/*** ID ***/

#component-root {
  padding: 0.5rem;
  margin-right: 1rem;
}

#nav {
  margin: 2rem;
  border-radius: 2rem;
  padding: 0.5rem;
  background-color: #f9f3ed;
  text-align: center;
  align-self: center;
}

.wrapper {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
}

.profile-img {
  width: 70%;
  height: auto;
  border-radius: 50%;
  margin-top: 5rem;
}

.menu-link {
  color: #654062;
  text-decoration: none;
  font-family: 'JetBrains Mono', monospace;
  font-size: 1.25em;
  padding: 0.25rem;
  margin: 1rem;
  transition: all 50ms linear;
  border-bottom: #654062 0px solid;
}

.menu-link:hover {
}

.menu-link::after {
  border: blue 2px solid;
  color: blue;
  height: 2px;
  width: 100%;
}

.menu-link:active {
  color: #86aba1;
}

a {
  color: #65d6ce;
  font-weight: bold;
}

.info {
  font-family: 'JetBrains Mono', monospace;
}

